import { Component } from "react";
import styled from "styled-components";

class BingoCell extends Component {
  render() {
    const { player, selectingNumber, number, bingoSet, activeklass } = this.props;
    return (
      <Button
        activeklass={activeklass}
        onClick={this._handlePlayer(player, bingoSet.turn, selectingNumber)}
      >
        {number}
      </Button>
    );
  }

  _handlePlayer = (player, turn, selectingNumber) => {
    if ((player === "player1" && turn) || (player === "player2" && !turn)) {
      return selectingNumber;
    } else if ((player === "player1" && turn) || (player === "player2" && !turn)) {
      console.log(11);
    } else {
      return null;
    }
  };
}

const Button = styled.button`
  width: 20%;
  height: 20%;
  float: left;
  border: 1px solid #333;
  background-color: ${props => {
    if (props.activeklass) return "#287ac6";
  }};
  outline: none;
  color: ${props => {
    if (props.activeklass) return "#fff";
  }};
`;

export default BingoCell;
